import React from 'react';
import { initialState, reducer } from '../drag-and-drop/reducers';
import * as actions from '../drag-and-drop/actions';
import { ActionTypes } from '../drag-and-drop/types';

// import { teamMembers, memberId } from '../../config/jest/mockData';

describe('drag-drop actions', () => {
  it('should create an action to select a team member id', () => {
    const memberId = '18000003';
    const expectedAction = {
      type: ActionTypes.ADD_TEAM_MEMBER,
      payload: { memberId }
    };
    expect(actions.addTeamMember(memberId)).toEqual(expectedAction);
    // expect(reducer(initialState, selectTeamMember(memberId))).toMatchSnapshot();
  });
  it('should create an action to remove a team member id', () => {
    const memberId = '18000003';
    const expectedAction = {
      type: ActionTypes.REMOVE_TEAM_MEMBER,
      payload: { memberId }
    };
    expect(actions.removeTeamMember(memberId)).toEqual(expectedAction);
  });
});

describe('drag-drop reducers', () => {
  it('should return the initial state', () => {
    expect(reducer(undefined, {})).toEqual({
      teamMembers: [],
      error: ''
    });
  });
  it('should handle ADD_TEAM_MEMBER', () => {
    expect(
      reducer(
        {
          teamMembers: [],
          error: ''
        },
        {
          type: ActionTypes.ADD_TEAM_MEMBER,
          payload: {
            memberId: '1800004'
          }
        }
      )
    ).toEqual({
      teamMembers: [{ id: '1800004' }],
      error: ''
    });
  });

  it('should handle REMOVE_TEAM_MEMBER', () => {
    expect(
      reducer(
        {
          teamMembers: [{ id: '1800004' }, { id: '1800008' }],
          error: ''
        },
        {
          type: ActionTypes.REMOVE_TEAM_MEMBER,
          payload: {
            memberId: '1800004'
          }
        }
      )
    ).toEqual({
      teamMembers: [{ id: '1800008' }],
      error: ''
    });
  });
});
